<template>
	<view>
		<template v-if="list.length">
			<!-- 有内容 -->
			<block v-for="(item, index) in list" :key="index">
				<index-list :item="item" :index="index"></index-list>
			</block>

			<!-- 上拉加载 -->
			<load-text :loadText="loadText"></load-text>
		</template>
		<template v-else-if="isSearch && !list.length">
			<!-- 无内容 -->
			<no-thing></no-thing>
		</template>
	</view>
</template>

<script>
	import IndexList from '../../components/index/index-list.vue'
	import LoadText from '../../components/load-more/load-more.vue'
	import NoThing from '../../components/no-thing/no-thing.vue'
	import {
		deepCopy
	} from '../../utils/utils.js'
	export default {
		components: {
			IndexList,
			NoThing,
			LoadText
		},
		data() {
			return {
				loadText: '上拉加载更多',
				loadTimes: 2, //模拟两次后没有数据
				list: [],
				isSearch: false,
				searchText: ''
			}
		},
		onNavigationBarButtonTap(e) { //监听原生标题栏点击事件
			// console.log(e)
			if (e.index === 0) { //点击第一个按钮 ， index来区分按钮 
				uni.navigateBack({
					delta: 1
				})
			}
		},
		onNavigationBarSearchInputChanged(e) { //监听原生搜索框文本内容变化
			// console.log("内容变化",e)
			// e.text  是搜索框内容
			if(e.text){
				this.searchText = e.text;
			}
			
		},
		onNavigationBarSearchInputConfirmed(e) { //点击键盘上搜索按钮
			// console.log("确定搜索",e)
			// e.text  搜索框内容
			if(e.text){
				uni.hideKeyboard()
				this.searchText = e.text;
				this.getData()
			}
		},
		onReachBottom() { //页面触底事件
			this.loadMore()
		},
		onPullDownRefresh() {//下拉刷新
			this.getData()
			
		},
		methods: {
			loadMore(index) { //上拉加载
				if (this.loadTimes < 0) {
					this.loadText = '没有更多数据了';
					return
				}
				if (this.loadText !== '上拉加载更多') return
				this.loadText = '正在加载中。。。';

				setTimeout(() => {
					let list = this.list;

					this.list.push(list[0], list[1])

					this.loadText = '上拉加载更多';
					this.loadTimes--;
				}, 1000)
			},
			getData(val) {
				uni.showLoading()
				
				setTimeout(() => {
					let list = [{ //图文
							userPic: '../../static/demo/userpic/12.jpg',
							userName: '王大锤',
							isguanzhu: true,
							title: '今天心情记录',
							type: "image", // image 和 video  两种类型
							titlePic: '../../static/demo/datapic/11.jpg', //封面图
							infoNum: {
								index: 1, //0未顶踩  1顶   2踩
								dingNum: 100,
								caiNum: 20
							},
							commentNum: 20,
							shareName: 10
						},
						{ //视频
							userPic: '../../static/demo/userpic/12.jpg',
							userName: '王大锤',
							isguanzhu: false,
							title: '今天心情记录',
							type: "video", // image 和 video  两种类型
							titlePic: '../../static/demo/datapic/11.jpg',
							playNum: "20W",
							times: "2.47",
							infoNum: {
								index: 2, //0未顶踩  1顶   2踩
								dingNum: 100,
								caiNum: 20
							},
							commentNum: 20,
							shareName: 10
						},
						{ //图文
							userPic: '../../static/demo/userpic/12.jpg',
							userName: '王大锤',
							isguanzhu: true,
							title: '今天心情记录',
							type: "image", // image 和 video  两种类型
							titlePic: '../../static/demo/datapic/11.jpg', //封面图
							infoNum: {
								index: 0, //0未顶踩  1顶   2踩
								dingNum: 100,
								caiNum: 20
							},
							commentNum: 20,
							shareName: 10
						}
					];
					this.list = list;
					// this.list = [];
					uni.hideLoading();
					uni.stopPullDownRefresh()
					this.isSearch = true;
				}, 3000)
			}
		}
	}
</script>

